body .l-header {
  background-color: unset;
  margin: 0px 0px 20px;
}

.l-header-inner.container {
  width: 100%;
  padding: 0px;
  margin: 0px;
  border: 0px solid green;
  position: relative;
  background-color: none;
}

.container {
  max-width: 100%;
}

// Positioning the blocks inside the menu header without admin bar

header .block-block-site-logo,
header .block.block-system-main-menu {
  position: fixed;
  top: 0px;
}

// Positioning the blocks inside the menu header WITH admin bar

.admin-bar header .block-block-site-logo,
.admin-bar header .block.block-system-main-menu {
  top: 33px;
}

.front header .block.block-system-main-menu {
  background-color: transparent;
  transition: all .7s ease-in;
}

.front header .block.block-system-main-menu.add-background {
  background-color: #094b70;
  opacity: .7;
  transition: all .7s ease-in;
}

.scrolling header .block.block-system-main-menu {
  height: 70px;
  transition: all .7s ease-in;
}

header .block.block-block-site-logo {
  height: 110px;
  z-index: 550;
  transition: all .7s ease-in;
  img {
    position: absolute;
    bottom: 20px;
    max-width: 200px;
    height: auto;
    left: 100px;
    z-index: 600;
    //-webkit-filter: drop-shadow(0px 0px 9px rgba(150, 150, 150, 0.86));
    //filter: drop-shadow(0px 0px 9px rgba(150, 150, 150, 0.86));
    transition: all .7s ease-in;
  }
}

.scrolling header .block.block-block-site-logo {
  height: 70px;
  z-index: 550;
  transition: all .7s ease-in;
  img {
    position: absolute;
    bottom: 10px;
    max-width: 150px;
    height: auto;
    left: 100px;
    z-index: 600;
    //-webkit-filter: drop-shadow(5px 5px 5px #222);
    //filter: drop-shadow(5px 5px 5px #222);
    transition: all .7s ease-in;
  }
}

header .block.block-system-main-menu {
  border: 0px solid yellow;
  width: 100%;
  background-color: #094b70;
  opacity: .7;
  z-index: 500;
  height: 110px;
  transition: all .7s ease-in;
}

header .block.block-system-main-menu div.block-content > ul {
  position: absolute;
  right: 100px;
  bottom: 10px;
  font-size: 12px;
  text-align: right;
  //text-shadow: 0px 0px 9px rgba(150, 150, 150, 0.86);
}

  // Hero Block on Front Page

.front .l-header-inner.container {
  height: 700px;
  padding: 0px;
}

.front .container .block-hero {
  height: 700px;
  padding: 20px;
  width: 100%;
  position: absolute;
  top: 0px;
  background-position: center bottom;
  background-size: cover;
}

.block-hero .hero-text {
  font-family: "Roboto Slab",Helvetica,serif;
  font-weight: 600;
  position: absolute;
  bottom: 30%;
  left: 0px;
  width: 80%;
  margin: 0px 10%;
  .hero-text-top {
    width: 100%;
    text-align: left;
    font-size: 4rem;
    line-height: 4rem;
  }
  .hero-text-bottom {
    width: 100%;
    text-align: right;
    font-size: 3rem;
    line-height: 3rem;
  }
}

